.card {
    max-width: 220px;
    max-height: 260px;
    font-size: 14px;
    display: flex;
    background-color: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    gap: 10px;

    &:hover {
        cursor: pointer;
        
        img {
            transition: all 0.4s ease;
            transform: scale(1.05);
        }
    }

    &__img {
        width: 150px;
        height: 130px;
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__price, &__country, &__desc {
        font-weight: 700;
        align-self: flex-end;
    }

}

.error {
    color: #6b0a09;
    margin: 25% auto 15% auto;
}