.nav {
    font-size: 12px;
}

.list__item_link { 
    margin-right: 40px;
    position: relative;
    &:first-child {
      &::after {
        content: '';
        position: absolute;
        top: -13px;
        left: -29px;
        width: 35px;
        height: 35px;
        background-image: url('../../img/icons/logo.svg');
        background-size: cover;
        background-repeat: no-repeat;
      }
    }
    &::before {
      content: '';
      position: absolute;
      // background-color: #7e0d0d;+
      background-color: #fff;
      top: 150%;
      right: 100vh;
      width: 100%;
      height: 1px;
      transition: right 0.4s ease-out;
    }

    &:hover {
      cursor: pointer;
      &::before {
        right: 0;
      }
    }
}

.black {
  &:first-child {
    &::after {
          background-image: url('../../img/icons/logo_black.svg');
          top: -20px;
          left: -28px;
        }
      }
  &:hover {
    &::before {
      right: 0;
      background-color: #000;
    }
  }
}
  
.elem {
    position: relative;
    width: 30px;
    height: 30px;
    display: block;
    margin: 0 auto 35px auto;
    background-repeat: no-repeat;
    background-size: cover;
  
    &::before, 
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: -55px;
        width: 60px;
        height: 1px;
        transform: translateX(-50%);
    }
    &::after {
        left: 80px;
    }
}
  
.elem_white {
    background-image: url('../../img/icons/coffee_icon_white.svg');
    &::before, 
    &::after {
        background-color: #fff;
    }
}
  
.elem_black {
    background-image: url('../../img/icons/coffee_icon_black.svg');
    &::before, 
    &::after {
      background-color: #000;
    }
}