.filter {
    display: flex;
    height: 30px;
    max-width: 800px;
    font-size: 14px;
    gap: 80px;
    flex-wrap: wrap;
    @media (max-width: 768px) {
        gap: 5px;
    }
    // .label {
        // max-width: 90px;
        // &:last-child {
        //     margin-right: 30px;
        // }
    // }

    &__name {
        max-width: 285px;
        display: flex;
        gap: 25px;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 768px) {
            flex: 1;
            gap: 5px;
            justify-content: initial;
        }

        .input {
            box-shadow: 0px 4px 4px #c8c8c840;
            padding: 5px;
            text-align: center;
            transition: all 0.3s ease;
            &:focus {
                box-shadow: 0px 4px 4px #00000040;
            }
        }
    }

    &__country {
        max-width: 350px;
        display: flex;
        gap: 35px;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 768px) {
            gap: 5px;
            justify-content: initial;
            margin-bottom: 10px;
            z-index: 1;
        }
    }

    .btn__group {
        display: flex;
        max-width: 210px;
        justify-content: space-between;
        gap: 5px;
        .button {
            width: 75px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            // margin-right: 5px;
            background: none;
            box-shadow: 0px 4px 4px #c6c6c640;
            transition: all 0.3s ease;
    
            &:hover {
                box-shadow: 0px 4px 4px #00000040;
            }
        }
    }
}