.shop {
    margin: 0 auto 40px auto;
    padding: 20px;
    display: flex;
    // min-height: 500px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url('../../img/main/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;

    &__shop {
        background-image: none;
    }

    &__item {
        background-image: none;
        margin-bottom: 0;
    }

    &__title {
        font-weight: 700;
        margin-bottom: 70px;
    }
    
    & .cards__container {
        padding: 5px;
        flex-wrap: wrap;
        justify-content: center;
        max-width: 920px;
        display: flex;
        gap: 20px;

        &__shop {
            max-width: 800px;
        }

        &__item {
            display: flex;
        }
    }

    .hidden {
        display: none;
    }
}