.header {
    min-height: 680px;
    margin: 0 auto 80px auto;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;   

    @media (max-width: 768px) {
        margin: 0 auto 30px auto;
    }
    &__nav {
        max-width: 1170px;
        margin: 0 auto;
        padding: 50px 0 0 0;
        margin-bottom: 140px;

        @media (max-width: 1250px) {
            padding: 40px 0 0 30px;
            margin-bottom: 120px;
        }

        @media (max-width: 768px) {
            padding: 40px 0 0 40px;
            margin-bottom: 60px;
        }
   }

   &__content {
        max-width: 920px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .content__title,
        .content__text {
            font-weight: 700;
            text-align: center;
        }
        .content__text {
            font-size: 24px;
            margin-bottom: 30px;
        }

        .content__title {
            margin-bottom: 20px;
            font-size: 40px;
        }

        .content__btn {
            background: transparent;
            color: #fff;
            font-weight: 700;
            font-size: 14px;
            width: 120px;
            height: 30px;
            border: 1px solid #FFFFFF;
            box-shadow: 0px 4px 4px #00000040;
            border-radius: 3px;
            transition: all 0.3s ease;

            &:hover {
                transform: scale(1.15);
                box-shadow: 0px 4px 4px #00000090;
            }
        }
   }
}
