.about {
    display: flex;
    max-width: 920px;
    margin: 0 auto 60px;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__title {
        margin-bottom: 25px;
        text-align: center;
    }

    &__elem {
        margin-bottom: 40px;
    }

    &__text {
        max-width: 600px;
        text-align: center;
        font-size: 14px;
        margin-bottom: 10px;
        font-weight: 400;

        &_price {
            font-size: 24px;
            line-height: 34.66px;
        }

        &_first {
            margin-bottom: 25px;
        }
        &::first-letter {
            margin-left: 15px;
        }
    }

    &__item {

        &_block {
            max-width: 420px;
        }
        
        &_text {
            font-size: 14px;
            font-weight: 700;
        }
        &_content {
            display: flex;
            gap: 55px;
        }

        
    }

    &__shop {
        position: relative;
        max-width: 800px;
        gap: 80px;
        flex-direction: row;
        margin-bottom: 60px;

        @media (max-width: 768px) {
            flex-direction: column;
            gap: 10px;
        }
        &::after {
            position: absolute;
            content: '';
            background-color: #000;
            width: 240px;
            height: 1px;
            bottom: -40px;
        }
    }

    &__content {
        max-width: 355px;
        flex-direction: column;
    }

    &__img {
        max-height: 355px;
        max-width: 270px;

        @media (max-width: 768px) {
            order: 1;
        }
        &_item {
            img {
                max-height: 355px;
                width: 400px;
            }
        }
    }

    img {
        object-fit: cover;
    }
}